.author-div {
    display: flex;
    align-items: center;
    margin-left: 1.4rem;
    padding: .4rem 0;
}

.author-pic {
    width: 3rem;
    padding: 0 0 0 1rem;
}

.author-name {
    font-size: 1rem;
    padding: 0 1rem;
    font-weight: bold;
}

.edit-btn-div {
    padding: 1.5rem 1rem;
}

.edit-btn {
    width: 1.5rem;
}

.edit-btn:hover {
    -webkit-animation: shake .4s cubic-bezier(.36, .07, .19, .97) both;
            animation: shake .4s cubic-bezier(.36, .07, .19, .97) both;
    -webkit-backface-visibility: hidden;
            backface-visibility: hidden;
}

.post-text {
    font-size: 1.5rem;
    padding: 2rem 2.5rem;
}

.post-footer {
    display: flex;
    justify-content: space-between;
    padding: 2rem 0 1rem 1.5rem;
    color: #373534;
}

.like-dislike-div {
    display: flex;
}

.like-div {
    -webkit-transform: scaleX(-1) translateY(-.5rem);
            transform: scaleX(-1) translateY(-.5rem);
}

.like-counter {
    font-size: 1rem;
    color: #373534;
    font-weight: bold;
    padding: 0 .5rem;
    -webkit-transform: translateY(-2px);
            transform: translateY(-2px);
}


