.green-txt {
    color: #01937C;
}

.red-txt {
    color: #962D2D;
}

@-webkit-keyframes shake {
    0% {
        -webkit-transform: rotate(0) scale(100%);
                transform: rotate(0) scale(100%);
    }
    15% {
        -webkit-transform: rotate(5deg) scale(103%);
                transform: rotate(5deg) scale(103%);
    }
    30% {
        -webkit-transform: rotate(-5deg) scale(106%);
                transform: rotate(-5deg) scale(106%);
    }
    45% {
        -webkit-transform: rotate(4deg) scale(109%);
                transform: rotate(4deg) scale(109%);
    }
    60% {
        -webkit-transform: rotate(-4deg) scale(112%);
                transform: rotate(-4deg) scale(112%);
    }
    75% {
        -webkit-transform: rotate(2deg) scale(115%);
                transform: rotate(2deg) scale(115%);
    }
    85% {
        -webkit-transform: rotate(-2deg) scale(116%);
                transform: rotate(-2deg) scale(116%);
    }
    92% {
        -webkit-transform: rotate(1deg) scale(118%);
                transform: rotate(1deg) scale(118%);
    }
    100% {
        -webkit-transform: rotate(0) scale(120%);
                transform: rotate(0) scale(120%);
    }
}

@keyframes shake {
    0% {
        -webkit-transform: rotate(0) scale(100%);
                transform: rotate(0) scale(100%);
    }
    15% {
        -webkit-transform: rotate(5deg) scale(103%);
                transform: rotate(5deg) scale(103%);
    }
    30% {
        -webkit-transform: rotate(-5deg) scale(106%);
                transform: rotate(-5deg) scale(106%);
    }
    45% {
        -webkit-transform: rotate(4deg) scale(109%);
                transform: rotate(4deg) scale(109%);
    }
    60% {
        -webkit-transform: rotate(-4deg) scale(112%);
                transform: rotate(-4deg) scale(112%);
    }
    75% {
        -webkit-transform: rotate(2deg) scale(115%);
                transform: rotate(2deg) scale(115%);
    }
    85% {
        -webkit-transform: rotate(-2deg) scale(116%);
                transform: rotate(-2deg) scale(116%);
    }
    92% {
        -webkit-transform: rotate(1deg) scale(118%);
                transform: rotate(1deg) scale(118%);
    }
    100% {
        -webkit-transform: rotate(0) scale(120%);
                transform: rotate(0) scale(120%);
    }
}

@media only screen and (max-width: 600px) {
    * {
        font-size: 10px;
    }
    body {
        min-width: 0px;
    }
    .post-div {
        width: 70%;
    }
    .post-text {
        font-size: 1.2rem;
    }
    .author-div {
        padding: .6rem 0;
    }
}
